/* This part is a modification using the react-transition-group dependency 
   To apply animation go to location where Routing is applied.

*/

.page-enter {
  opacity:0.01;
  transform: translateX(-100%);
}
.page-enter-active {
  opacity:1;
  transform: translateX(0%);
  transition: all 300ms ease-in;
}
.page-exit {
  opacity: 1;
  transform: translateX(0%);
}

.page-exit-active {
  opacity: 0.01;
  transform: translateX(100%);
  transition: all 300ms ease-out;

}
